@import url("https://fonts.googleapis.com/css2?family=Montserrat&display=swap");

* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    font-family: "Montserrat", sans-serif;
    background-color: #1a1a1c;
    color: #fff;
}

.coinApp {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 64px;
    color: #fff;
}

.coinSearch {
    margin-bottom: 64px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.coinText {
    margin-bottom: 32px;
    text-align: center;
}

.coinInput {
    width: 300px;
    height: 50px;
    border-radius: 4px;
    border: 2px;
    background-image: linear-gradient(
        -225deg,
        #ac32e4 0%,
        #7918f2 48%,
        #4801ff 100%
    );
    color: #e2e2e2;

    font-size: 25px;
    text-align: center;
    font-weight: 500;
}

.coinInput::placeholder {
    color: #e2e2e2;
    text-align: center;
    font-size: 25px;
    font-weight: 500;
}
