@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    font-family: "Montserrat", sans-serif;
    background-color: #1a1a1c;
    color: #fff;
}

.coinApp {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 64px;
    color: #fff;
}

.coinSearch {
    margin-bottom: 64px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.coinText {
    margin-bottom: 32px;
    text-align: center;
}

.coinInput {
    width: 300px;
    height: 50px;
    border-radius: 4px;
    border: 2px;
    background-image: linear-gradient(
        -225deg,
        #ac32e4 0%,
        #7918f2 48%,
        #4801ff 100%
    );
    color: #e2e2e2;

    font-size: 25px;
    text-align: center;
    font-weight: 500;
}

.coinInput:-ms-input-placeholder {
    color: #e2e2e2;
    text-align: center;
    font-size: 25px;
    font-weight: 500;
}

.coinInput::placeholder {
    color: #e2e2e2;
    text-align: center;
    font-size: 25px;
    font-weight: 500;
}

.coinContainer {
    display: flex;
    justify-content: center;
}

.coinRow {
    display: flex;
    flex-direction: row;
    justify-items: start;
    align-items: center;
    height: 80px;
    border-bottom: 1px solid whitesmoke;
    width: 900px;
}

.coin {
    display: flex;
    align-items: center;
    padding-right: 24px;
    min-width: 300px;
}

.coin h1 {
    font-size: 16px;
    width: 150px;
}

.coin img {
    height: 30px;
    width: 30px;
    margin-right: 10px;
}

.coinSymbol {
    text-transform: uppercase;
}

.coinData {
    display: flex;
    text-align: center;
    justify-content: space-between;
    width: 100%;
}

.coinPrice {
    width: 110px;
}

.coinVolume {
    width: 200px;
}

.coinMarketcap {
    width: 240px;
}

.coinPercent {
    width: 80%;
}

.red {
    color: #f00606;
}

.green {
    color: #11d811;
}

